.backdrop {
    top: 0;
    left: 0;
    position: fixed;
    width: 100%;
    height: 100vh;
    z-index: 10;
    background: rgba(0, 0, 0, 0.75);
}

.modal {
    position: fixed;
    top: 30vh;
    left: 10%;
    width: 80%;
    z-index: 100;
    overflow: hidden;

}

.header {
    background: #4f005d;
    padding: 1rem;

}

.header h1 {
    color: white;
    margin: 0;
}

.content {
    padding: 1rem;
}

.action {
    padding: 1rem;
    display: flex;
    justify-content: flex-end;
}

@media(min-width: 768px) {
    .modal {
        left: calc(50% - 20rem);
        width: 40rem;
    }


}